export default {
	data() {
		return {
			nickName: '',
			openid: '',
			session_key: '',
			avatarUrl: 'https://ws1.sinaimg.cn/large/007RvR0Hly1gack42dh16j307g0b4q6o.jpg',
			current_skin: '#4191ea',
		};
	},
	onShow() {
		this.current_skin = uni.getStorageSync('current_skin');
		this.avatarUrl = uni.getStorageSync('avatarUrl');
		this.nickName = uni.getStorageSync('nickName');
	},
	mounted() {
		this.current_skin = uni.getStorageSync('current_skin');
		this.avatarUrl = uni.getStorageSync('avatarUrl');
		this.nickName = uni.getStorageSync('nickName');
	},
	onLoad() {
		this.current_skin = uni.getStorageSync('current_skin');
		this.avatarUrl = uni.getStorageSync('avatarUrl');
		this.nickName = uni.getStorageSync('nickName');
	},
	methods: {
		changeSkin(){
			uni.navigateTo({
					url: '../skin-change/skin-change'
			});
		},
		ClickToInfo(){
			uni.navigateTo({
					url: '../info/info'
			});
		}
	}
}
